<script>
/* eslint-disable no-undef */

import {messageCollection, projectCollection, firebase} from "@/firebase";
import Modal from "@/components/Modal";
import Slider from "@/components/Slider";

export default {
  name: 'Home',
  components: {
    Slider,
    Modal
  },
  data() {
    return {
      projects: [],
      message: {
        'email': '',
        'name': '',
        'message': ''
      },
      loading: false,
      showPortfolio: false,
      project: {}
    }
  },
  methods: {
    scrollFix: function (hash) {
      setTimeout(() => $('html, body').animate({
        // eslint-disable-next-line no-undef
        scrollTop: $(hash).offset().top
      }, 1000), 1)
    },
    async fetchProjects() {
      const snapshot = await projectCollection.get()
      await snapshot.docs.map(doc => {
        this.projects.push(doc.data())
        console.log(doc.data())
      });

      var $workGrid = $('.portfolio-grid').isotope({
        itemSelector: '.all',
        percentPosition: true,
        masonry: {
          columnWidth: '.all'
        }
      });

      $('.portfolio-filter ul li').on('click', function () {

        console.log("clicked");

        // eslint-disable-next-line no-undef
        $('.portfolio-filter ul li').removeClass('active');
        // eslint-disable-next-line no-undef
        $(this).addClass('active');

        var data = $(this).attr('data-filter');
        $workGrid.isotope({
          filter: data
        });
      });

      setTimeout(function () {
        // eslint-disable-next-line no-undef
        $('.refresh').trigger("click");
      }, 2000)

    },
    storeMessage() {

      if (!this.message.name || !this.message.email || !this.message) {
        return;
      }

      // eslint-disable-next-line no-undef
      $('#messageDialog').modal({
        backdrop: 'static'
      })

      this.loading = true
      var _this = this
      this.message.created = firebase.firestore.FieldValue.serverTimestamp()
      messageCollection.add(this.message)
          .then(docRef => {
            _this.loading = false
            _this.message = {}
            console.log(docRef.id)
          })
          .catch(err => {
            console.log(err)
            _this.loading = false
          });
    },
    keywordsToStr(item) {
      return _.map(item, function (i) {
        return ' ' + i.toLowerCase();
      });
    },
    showProject(project) {
      this.showPortfolio = true
      this.project = project
      document.body.classList.add("modal-open");
    },
    hideProject() {
      document.body.classList.remove("modal-open");
      this.showPortfolio = false
      this.project = {}
    }
  },
  computed: {
    keywords: function () {
      // eslint-disable-next-line no-undef

      let keywords = _.flatMap(_.map(this.projects, 'keywords'))
          .join('|')
          .toLowerCase()
          .split('|')
      return _.uniq(keywords);
    }
  },
  created() {
    this.fetchProjects()
  },
  mounted() {
    window.document.onscroll = () => {
      let nav_offset_top = $('header').height() + 50;

      // eslint-disable-next-line no-undef
      var scroll = $(window).scrollTop();
      if (scroll >= nav_offset_top) {
        // eslint-disable-next-line no-undef
        $('.header_area').addClass('navbar_fixed');
      } else {
        // eslint-disable-next-line no-undef
        $('.header_area').removeClass('navbar_fixed');
      }


      //nav match
      // eslint-disable-next-line no-undef
      $('.content_section').each(function () {
        var sectionName = $(this).attr("id");
        var navMatch = $('.nav-item a[id="sc' + sectionName + '"]')

        // console.log(sectionName);
        // console.log(navMatch);

        // eslint-disable-next-line no-undef
        if ($(this).offset().top - $(window).height() / 2 < $(window).scrollTop() &&
            $(this).offset().top + $(this).height() - $(window).height() / 2 > $(window).scrollTop()) {
          navMatch.parent().addClass("active")
        } else {
          navMatch.parent().removeClass("active")
        }

      })

    }

    /*    if ($('.testi_slider').length) {
          $('.testi_slider').owlCarousel({
            loop: true,
            margin: 30,
            items: 2,
            autoplay: true,
            smartSpeed: 2500,
            dots: true,
            responsiveClass: true,
            responsive: {
              0: {
                items: 1
              },
              991: {
                items: 2
              }
            }
          });
        }*/

    $('.nav-link').on('click', function (event) {
      event.preventDefault();

      $('#navbarSupportedContent').removeClass('show');
      $('.navbar-toggler')
          .attr('aria-expanded', 'false')
          .addClass('collapsed');
    });

  }
}
</script>

